"use client";
import { useState, useMemo } from "react";
import { saveAs } from "file-saver";

export default function StudentTable({ students }) {
  const [search, setSearch] = useState("");
  const [sortField, setSortField] = useState("name");
  const [sortOrder, setSortOrder] = useState("asc");
  const [page, setPage] = useState(1);
  const pageSize = 10;

  // Filter + Sort
  const filteredStudents = useMemo(() => {
    let data = students.filter((s) =>
      s.name.toLowerCase().includes(search.toLowerCase())
    );

    data.sort((a, b) => {
      let valA = a[sortField];
      let valB = b[sortField];
      if (typeof valA === "string") valA = valA.toLowerCase();
      if (typeof valB === "string") valB = valB.toLowerCase();
      if (valA < valB) return sortOrder === "asc" ? -1 : 1;
      if (valA > valB) return sortOrder === "asc" ? 1 : -1;
      return 0;
    });

    return data;
  }, [students, search, sortField, sortOrder]);

  // Pagination
  const totalPages = Math.ceil(filteredStudents.length / pageSize);
  const paginated = filteredStudents.slice(
    (page - 1) * pageSize,
    page * pageSize
  );

  // CSV Export
  const handleDownload = () => {
    const csvRows = [
      ["Name", "Assessment Score"],
      ...filteredStudents.map((s) => [s.name, s.assessment_score]),
    ];
    const csvData = csvRows.map((row) => row.join(",")).join("\n");
    const blob = new Blob([csvData], { type: "text/csv;charset=utf-8;" });
    saveAs(blob, "students.csv");
  };

  // Sort Toggle
  const handleSort = (field) => {
    if (sortField === field) {
      setSortOrder(sortOrder === "asc" ? "desc" : "asc");
    } else {
      setSortField(field);
      setSortOrder("asc");
    }
  };

  return (
    <div className="w-full bg-white rounded-xl shadow-lg p-4 text-gray-800">
      <div className="flex flex-col md:flex-row justify-between items-center mb-4 gap-2">
        <input
          type="text"
          placeholder="Search by name..."
          className="border rounded-lg px-3 py-2 w-full md:w-1/3 text-gray-800"
          value={search}
          onChange={(e) => {
            setSearch(e.target.value);
            setPage(1);
          }}
        />
        <button
          onClick={handleDownload}
          className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition"
        >
          Download CSV
        </button>
      </div>

      <table className="w-full border-collapse rounded-lg overflow-hidden">
        <thead>
          <tr className="bg-gray-100 text-gray-700">
            <th
              className="cursor-pointer px-4 py-2 text-left"
              onClick={() => handleSort("name")}
            >
              Name {sortField === "name" ? (sortOrder === "asc" ? "▲" : "▼") : ""}
            </th>
            <th
              className="cursor-pointer px-4 py-2 text-left"
              onClick={() => handleSort("assessment_score")}
            >
              Score{" "}
              {sortField === "assessment_score"
                ? sortOrder === "asc"
                  ? "▲"
                  : "▼"
                : ""}
            </th>
          </tr>
        </thead>
        <tbody>
          {paginated.map((student, idx) => (
            <tr
              key={idx}
              className="border-b hover:bg-gray-50 transition text-gray-800"
            >
              <td className="px-4 py-2">{student.name}</td>
              <td className="px-4 py-2">{student.assessment_score}</td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* Pagination */}
      <div className="flex justify-center items-center gap-2 mt-4">
        <button
          disabled={page === 1}
          onClick={() => setPage((p) => p - 1)}
          className="px-3 py-1 rounded bg-gray-200 hover:bg-gray-300 disabled:opacity-50"
        >
          Prev
        </button>
        <span className="text-gray-700">
          Page {page} of {totalPages}
        </span>
        <button
          disabled={page === totalPages}
          onClick={() => setPage((p) => p + 1)}
          className="px-3 py-1 rounded bg-gray-200 hover:bg-gray-300 disabled:opacity-50"
        >
          Next
        </button>
      </div>
    </div>
  );
}
